@import "sass/index";

$defaultMargin: 20px;

.box {
  width: 100%;
  margin-top: $defaultMargin;
}
.formBox {
  width: 100%;
  margin: 0;
}
.title {
  text-transform: uppercase;
  font-size: 0.875rem;
  margin: 0;

  @media (max-width: 0.45px) {
    font-size: 0.685rem;
  }
}
.checkbox {
  margin: 0;
  margin-left: calc(#{$spacing} * -2.4);
  color: use(--template-modal-title-color);
}
.check {
  color: use(--template-modal-title-color);
}

.label {
  color: use(--secondary-main);

  @media (max-width: 0.45px) {
    font-size: 0.75rem;
  }
}

.checkboxRoot {
  margin-top: 40px;
  margin-bottom: 10px;
}

.inputWrapper {
  position: relative;
  margin-left: 30px;

  & :first-child {
    margin-top: 0;
  }
}

.radio {
  &Label {
    color: use(--secondary-main);
  }
  &Radio {
    border-color: use(--template-modal-title-color);
  }
  &Round {
    background-color: use(--template-modal-title-color);
  }
}
