$wrapWidth: 450px;
$gap: 10px;

.section {
  margin-top: calc(#{$gap} * 2);
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: $gap;
  grid-gap: $gap;

  @media (max-width: $wrapWidth) {
    grid-template-columns: 1fr;
    margin-top: calc(#{$gap} * 3);
  }
}
.title {
  text-transform: uppercase;
  font-size: 0.875rem;
  margin: 0;

  @media (max-width: $wrapWidth) {
    grid-template-columns: 1fr;
  }
}
.inputWrapper {
  position: relative;
  & :first-child {
    margin: 0;
  }
}
